.greetings {
  color: white;
  font-family: 'Sofia', cursive;
  padding: 15px 15px 50px 15px;
  position: absolute;
  font-weight: 700;
  top: 45px;
  left: 50%;
  margin-left: -120px;
  border-radius: 15px;
  box-shadow: 0px 4px 6px 0px #0d1c05;
}

.greetings-icon {
  position: absolute;
  border-radius: 50%;
  left: 50%;
  margin-left: -1.2rem;
  top: 120px;
  border: none;
  font-size: 50px;
  -webkit-animation: iconBlink 3s ease-in 0s infinite;
          animation: iconBlink 3s ease-in 0s infinite;

}

.greetings-icon:hover {
  cursor: pointer;
  box-shadow: 0px 4px 15px 4px #0d1c05;
}

@-webkit-keyframes iconBlink {
  from { box-shadow: 0px 0px 2px 1px #ff5c2b; }
  50% { box-shadow: 0px 0px 10px 5px #e2400f; }
  to { box-shadow: 0px 0px 2px 1px #ff5c2b; }
}

@keyframes iconBlink {
  from { box-shadow: 0px 0px 2px 1px #ff5c2b; }
  50% { box-shadow: 0px 0px 10px 5px #e2400f; }
  to { box-shadow: 0px 0px 2px 1px #ff5c2b; }
}

