.animateValue {
	-webkit-animation-name: valueAppear;
	        animation-name: valueAppear;
	-webkit-animation-duration: 0.4s;
	        animation-duration: 0.4s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

@-webkit-keyframes valueAppear {
	from { -webkit-transform: scale(0); transform: scale(0); visibility: hidden;}
	to { -webkit-transform: scale(1); transform: scale(1); visibility: visible;}
}

@keyframes valueAppear {
	from { -webkit-transform: scale(0); transform: scale(0); visibility: hidden;}
	to { -webkit-transform: scale(1); transform: scale(1); visibility: visible;}
}

.squareInnerContainer h3 {
	font-size: 1.2rem;
	font-weight: 600;
