    margin: 0;
	color: rebeccapurple;
}

.squareIcon {
	position: absolute;
	color: white;
	background-color: #eb3548;
	right: 40%;
	font-size: 70px;
	border-radius: 50%;
	padding: 3px;
	box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.4);
	cursor: pointer;
}

.animateIcon {
	-webkit-animation-name: iconMove;
	        animation-name: iconMove;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-duration: 0.2s;
	        animation-duration: 0.2s;
	cursor: default;
}

@-webkit-keyframes iconMove {
	from{ -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
	to{ 
		-webkit-transform: translate(-110%, -85%) scale(0.55); 
		        transform: translate(-110%, -85%) scale(0.55);
	}
}

@keyframes iconMove {
	from{ -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
	to{ 
		-webkit-transform: translate(-110%, -85%) scale(0.55); 
		        transform: translate(-110%, -85%) scale(0.55);
	}
}

